export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const domain = process.env.NEXT_PUBLIC_AUTH0_ISSUER_BASE_URL || ""; // Asegurarse de que no sea undefined
  const clientId = process.env.AUTH0_CLIENT_ID || ""; // Asegurarse de que no sea undefined

  // Si no se encuentra alguna variable, puedes lanzar un error o manejarlo de otra forma
  if (!domain || !clientId) {
    throw new Error(
      "Auth0 domain or clientId is not defined in environment variables."
    );
  }

  return (
    <html lang="en">
      <Auth0Provider
        domain={domain}
        clientId={clientId}
        authorizationParams={{
          redirect_uri: window.location.origin, // Redirección después del login
        }}
      >
        <UserProvider>
          <AdminProvider>
            <SuscribeProvider>
              <body
                className={`${inter.variable} ${nacelle.variable} bg-gray-950 font-inter text-base text-gray-200 antialiased`}
              >
                <div className="flex min-h-screen flex-col overflow-hidden supports-[overflow:clip]:overflow-clip">
                  <Header />
                  {children}
                </div>
              </body>
            </SuscribeProvider>
          </AdminProvider>
        </UserProvider>
      </Auth0Provider>
    </html>
  );
}
